<template>
  <div>

<div class="mt-2">
<div class="table-responsive mt-4">
    <table class="table table-striped table-sm">
        <thead>
            <tr class="text-center fw-bold">
            <th scope="col">A Code</th>
            <th scope="col">Assignment Heading</th>
            <th scope="col">On Submitted</th>
            <!-- <th scope="col">Assignment Desc</th> -->
            <th scope="col">Marks</th>
            <th scope="col">Assignment Submitted Link</th>
            <th scope="col">Delete</th>
            <!-- <th scope="col">Delete</th> -->
            </tr>
            </thead>
            <tbody class="text-center ">
                <tr class="mt-2" v-for="(assignment, _id) in Assignments" :key="_id">

                <td>{{assignment.acode}}</td>
                <td>{{assignment.aheading}}</td>
                <td>{{assignment.date.split("T")[0]}}</td>
                <td>{{assignment.marks}}</td>
                <td><a :href="assignment.aslink" target="_blank" class="btn btn-info">Open Link</a></td>

                <!-- <td><button class="btn btn-primary"  data-bs-toggle="modal"
      data-bs-target="#SubmitAssignmentModal" @click="submitAssignment(_id)">Update</button></td> -->
                <td><button class="btn btn-danger" @click="deleteAssignment(_id)">Delete</button></td>
                
                </tr>
                
            </tbody>
    </table>
</div>
</div>


  </div>
</template>

<script>
 import { mapActions,mapGetters } from "vuex";
import axios from 'axios'
import config from "@/config"

export default {
name:'SubmittedAssignment',

data(){
    return{
        Assignments:[],
        
    }
},
computed:{
    ...mapGetters(["user"]),
},
methods:{
...mapActions(["getProfile"]),


//Get submitted assignment
 async getSubmittedAssignments(){
     
        let usernames = this.user.username
        
      
let {
    data
}= await axios.get(`${config.apiBaseUrl}/api/submissions/get-submitted-assignment?username=${usernames}` )

  this.Assignments = data
  console.log(data)



},

// Delete Assignment
     async deleteAssignment(_id){
      
       let id  = this.Assignments[_id]._id


      let res = await axios.delete(`${config.apiBaseUrl}/api/submissions/delete-submitted-assignment`,{data: { _id:id} })
  
  this.getSubmittedAssignments() //calling get submitted Assignment
            this.$toasted.show('Assignment deleted');
      if(res.data.success){
          console.log('Assignment Deleted')
        }
        return res;
     },


},
created(){
    this.getProfile();
  this.getSubmittedAssignments()
}


}
</script>

